import pagesJson from '@/pages.json'
const uniIdCo = uniCloud.importObject("uni-id-co")
const db = uniCloud.database();
const usersTable = db.collection('uni-id-users')



let hostUserInfo = uni.getStorageSync('uni-id-pages-userInfo')||{}
console.log( hostUserInfo);
const data = {
	userInfo: hostUserInfo,
	hasLogin: Object.keys(hostUserInfo).length != 0
}

console.log('data', data);
//  mutations, 
export const mutations = {
	// data(),
	async updateUserInfo(data = false) {
		if (data) {
			usersTable.where('_id==$env.uid').update(data).then(e => {
				console.log(e);
				if (e.result.updated) {
					uni.showToast({
						title: "",
						icon: 'none'
					});
					this.setUserInfo(data)
				} else {
					uni.showToast({
						title: "",
						icon: 'none'
					});
				}
			})
			
		} else {
			try {
				let res = await usersTable.where("'_id' == $cloudEnv_uid")
						.field('mobile,nickname,user_name,email,avatar_file')
						.get()
				console.log('fromDbData',res.result.data);
				this.setUserInfo(res.result.data[0])
			} catch (e) {
				this.setUserInfo({},{cover:true})
				console.error(e.message, e.errCode);
			}
		}
	},
	async setUserInfo(data, {cover}={cover:false}) {
		console.log('set-userInfo', data);
		let userInfo = cover?data:Object.assign(store.userInfo,data)
		store.userInfo = Object.assign({},userInfo)
		store.hasLogin = Object.keys(store.userInfo).length != 0
		console.log('store.userInfo', store.userInfo);
		uni.setStorage({
			key: "uni-id-pages-userInfo",
			data:store.userInfo
		})
		return data
	},
	async logout() {
		await uniIdCo.logout()
		uni.removeStorageSync('uni_id_token');
		uni.setStorageSync('uni_id_token_expired', 0)
		uni.redirectTo({
			url: `/${pagesJson.uniIdRouter?.loginPage ?? 'uni_modules/uni-id-pages/pages/login/login-withoutpwd'}`,
		});
		uni.$emit('uni-id-pages-logout')
		this.setUserInfo({},{cover:true})
	},
	loginSuccess(e){
		const {
			showToast = true, toastText = '', autoBack = true, uniIdRedirectUrl = ''
		} = e
		console.log({
			toastText,
			autoBack
		});
		if (showToast) {
			uni.showToast({
				title: toastText,
				icon: 'none'
			});
		}
		this.updateUserInfo()
		uni.$emit('uni-id-pages-login-success')
		if (autoBack) {
			let delta = 0; //
			let pages = getCurrentPages();
			// console.log(pages);
			pages.forEach((page, index) => {
				if (pages[pages.length - index - 1].route.split('/')[3] == 'login') {
					delta++
				}
			})
			console.log(':', pages, delta);
			if (uniIdRedirectUrl) {
				return uni.reLaunch({
					url: uniIdRedirectUrl
				})
			}






		
			if (delta) {
				const page = pagesJson.pages[0]
				return uni.reLaunch({
					url: `/${page.path}`
				})
			}
		
			uni.navigateBack({
				delta
			})
		}
	}
	
}


import Vue from 'vue'
// Vue.observable
export const store = Vue.observable(data)









